.block{position:fixed;top:0;right:-20%;background-image:url("../../../assets/img/footer-bg.png");background-repeat:no-repeat;background-size:cover;z-index:10;margin-right:calc(-1*(100vw - 100%));width:40%;transition:opacity 0s,visibility 0s,right 0s;opacity:0;height:100%;visibility:hidden;overscroll-behavior-y:none;overflow-y:auto}.block_active{right:0;transition:all .3s,opacity 0s;opacity:1;visibility:visible}.menuBg{position:fixed;height:100%;top:0;left:0;background-color:#0eb553;width:100vw;z-index:9;opacity:0;visibility:hidden}.menuBg_active{opacity:.3;visibility:visible;transition:all .3s}.wrapper{padding:0 max(40px,(100vw - 1400px)/2) 0 40px}.headerWrapper{border-bottom:1px solid hsla(0,0%,100%,.25)}.header{height:70px;display:flex;align-items:center;justify-content:space-between}.logo{width:100px;height:auto}.rightPart{display:flex;align-items:center}.menuTitle{font-size:14px;color:#fff;margin-right:15px}.closeWrapper{width:40px;height:40px;border-radius:8px;border:1px solid rgba(226,232,240,.25);display:flex;align-items:center;justify-content:center;cursor:pointer}.items{padding-top:32px}.itemWrapper{margin-bottom:18px;display:flex;justify-content:space-between}.itemWrapper:last-child{margin-bottom:0}.item{display:flex;align-items:center}.imgWrapper{width:24px;height:24px;display:flex;align-items:center;justify-content:center;margin-right:10px}.itemTitle{color:#fff;font-size:16px;line-height:110%}.firstItems{padding-bottom:30px;border-bottom:1px solid hsla(0,0%,100%,.25)}.secondItems{padding:30px 0}.create{display:flex;align-items:center}.create img{width:32px;height:32px}.createTitle{font-size:12px;line-height:150%;color:#0eb553;margin-right:15px}@media(max-width: 1199px){.wrapper{padding:0 20px}.block{width:60%}}